import { useEffect } from "react";

import Button from "../../common/Button";
import Link from "../../common/Link";
import SectionContainer from "../../common/SectionContainer";

import { useMainMonster } from "../../monster/MonsterProvider";
import Panel from "../../common/Panel";

export default function Policies() {
  const { setMonsterState, showMonsterBubble, hideMonsterBubble } =
    useMainMonster();

  useEffect(() => {
    setMonsterState("sweating");
    showMonsterBubble(
      <>Vous n'avez pas réellement tout lu, n'est-ce pas&nbsp;?</>
    );

    return () => {
      setMonsterState("walking");
      hideMonsterBubble();
    };
  }, [setMonsterState, showMonsterBubble, hideMonsterBubble]);

  return (
    <SectionContainer>
      <Panel title="Politique de Confidentialité" sx={{ maxWidth: 500 }}>
        <p>
          Cette application respecte la confidentialité des utilisateurs. Aucune
          donnée personnelle n'est collectée, stockée ou partagée lors de
          l'utilisation de cette application.
        </p>
      </Panel>

      <Panel title="Collecte de Données" sx={{ maxWidth: 500 }}>
        <p>
          Cette application ne recueille aucune information personnelle
          identifiable. Elle ne collecte ni n'utilise de données telles que des
          informations de localisation, des informations financières, ou tout
          autre type de données utilisateur.
        </p>
      </Panel>

      <Panel title="Utilisation de l'Application" sx={{ maxWidth: 500 }}>
        <p>
          L'application peut être utilisée sans aucune préoccupation concernant
          la collecte ou le partage de données personnelles.
        </p>
      </Panel>

      <Panel title="Modifications" sx={{ maxWidth: 500 }}>
        <p>
          Cette politique de confidentialité peut être mise à jour
          occasionnellement. Toute modification sera publiée sur cette page. Il
          est recommandé de consulter régulièrement cette page pour rester
          informé des éventuelles modifications.
        </p>
      </Panel>

      <Panel title="Contact" sx={{ maxWidth: 500 }}>
        <p>
          Pour toute question relative à cette politique de confidentialité,
          veuillez vous adresser à l'adresse suivante :{" "}
          <Link href="mailto:adrien.grt@gmail.com">adrien.grt@gmail.com</Link>.
        </p>
      </Panel>

      <Button
        variant="primary"
        size="small"
        component="a"
        href={process.env.IS_SITE ? "#splashscreen" : "#title"}
        sx={{ alignSelf: "center" }}
      >
        Retour
      </Button>

      <br />
    </SectionContainer>
  );
}
