import styled from "@mui/system/styled";
import Box from "@mui/system/Box";

const Separator = styled("li")({
  listStyle: "none",
  fontWeight: "bold",
  fontStyle: "italic",

  "&:before,&:after": {
    content: "' == '",
  },
});

const NoStyleLi = styled("li")({
  listStyle: "none",
});

const MysteriousRock = styled("button")({
  width: 24,
  height: 24,
  display: "inline-block",
  backgroundColor: "transparent",
  border: "none",
  appearance: "none",
  cursor: "pointer",
  backgroundImage: "url(./images/items.png)",
  backgroundPosition: "0 -9px",
  imageRendering: "pixelated",
  MozImageRendering: "crisp-edges",
  OImageRendering: "crisp-edges",
  WebkitImageRendering: "optimize-contrast",
  msInterpolationMode: "nearest-neighbor",
  transform: "scale(2)",
  animationName: "rockGlowing",
  animationTimingFunction: "steps(2)",
  animationIterationCount: "infinite",
  animationDuration: "1s",

  "&.is-active": {
    animationName: "rockExploding",
    animationTimingFunction: "ease-in-out",
    animationIterationCount: 1,
    animationDuration: "3s",
    animationFillMode: "forwards",
  },

  "@keyframes rockGlowing": {
    "0%": {
      backgroundPosition: "0 -10px",
    },
    "100%": {
      backgroundPosition: "0 -56px",
    },
  },

  "@keyframes rockExploding": {
    "0%": {
      opacity: 1,
      transform: "scale(2) rotate(0deg)",
    },

    "50%": {
      opacity: 1,
      transform: "scale(20) rotate(1080deg)",
    },

    "100%": {
      transform: "scale(0) rotate(-1080deg)",
    },
  },
});

export default [
  {
    name: "2.6.0",
    notes: (
      <>
        <Separator>Nouveautés</Separator>
        <li>
          Les mots vulgaires et/ou inapproprié ne seront plus sélectionnés en
          tant que mot-mystère, permettant de passer le jeu en <b>PEGI 3</b>
          &nbsp;!&nbsp;
          <i>(vous pouvez toujours les proposer en tant que suggestions)</i>
        </li>
        <Separator>Corrections de bugs</Separator>
        <li>
          L'archéologue n'a plus un double chapeau lors de son animation de
          joie.
        </li>
      </>
    ),
  },
  {
    name: "2.5.0",
    notes: (
      <>
        <Separator>Nouveautés</Separator>
        <li>
          Un nouveau succès fait son apparition, <b>"Fouiller le passé"</b>,
          avec sa propre apparence de monstre&nbsp;!
        </li>
        <li>
          Un chronomètre est désormais présent&nbsp;!
          <i>
            (si cela vous stresse trop de le voir en jouant, vous pouvez le
            cacher depuis les options)
          </i>
        </li>
        <li>
          Le succès <b>"C'est impossible&nbsp;!"</b> a été supprimé et remplacé
          par un nouveau : <b>"Qui a éteint la lumière&nbsp;?"</b>.{" "}
          <i>
            (trouver le mot-mystère en 3 coups ou moins était peut-être un peu
            trop impossible, en fait...&nbsp;!)
          </i>
        </li>
        <Separator>Corrections de bugs</Separator>
        <li>
          Le succès <b>"Vous avez triché, n'est-ce pas&nbsp;?"</b> est désormais
          déblocable dans le mode "Mot du jour"{" "}
          <i>(il est presque impossible de le débloquer sinon&nbsp;!)</i>
        </li>
      </>
    ),
  },
  {
    name: "2.4.0",
    notes: (
      <>
        <Separator>Nouveautés</Separator>
        <li>
          Lorsque vous trouvez un anagramme, le petit monstre vous félicite et
          ré-explique le rôle des lettres marquées en bleu.{" "}
          <i>(ceci devrait rendre le tout plus clair&nbsp;!)</i>
        </li>
        <li>
          Le vieux monstre a désormais une canne.{" "}
          <i>(il en avait bien besoin&nbsp;!)</i>
        </li>
        <li>
          Un nouveau succès fait son apparition, <b>"Rapide comme l'éclair"</b>,
          avec sa propre apparence de monstre&nbsp;!
        </li>
        <Separator>Corrections de bugs</Separator>
        <li>
          À la fin du tutoriel, si le joueur a du mal à trouver le mot-mystère,
          le petit monstre ne répète plus en boucle le même message.
        </li>
        <li>
          Correction d'un bug à cause duquel le vieux monstre pouvait parfois se
          téléporter.{" "}
          <i>
            (il marche tellement lentement qu'il brisait parfois
            l'espace-temps&nbsp;!)
          </i>
        </li>
      </>
    ),
  },
  {
    name: "2.3.1",
    notes: (
      <>
        <Separator>Nouveautés</Separator>
        <li>
          Un nouveau succès fait son apparition, avec sa propre apparence de
          monstre&nbsp;! <i>(allez-vous trouver comme le débloquer&nbsp;?)</i>
        </li>
        <li>
          Les lettres bleues, qui indiquent une lettre correctement placée dans
          le mot-mystère et qui apparaissent lorsque vous trouvez un anagramme,
          ne sont plus dévoilées aléatoirement.
        </li>
        <Separator>Corrections de bugs</Separator>
        <li>
          Certains joueurs pouvaient avoir des soucis pour revenir dans une
          partie en cours, suite à la mise à jour précédente. Ceci devrai être
          réglé.
        </li>
        <li>
          En cas de bug majeur <i>(comme le précédent&nbsp;!)</i>, le jeu
          n'affiche plus un simple écran vide mais un autre un peu plus
          sympathique.
        </li>
      </>
    ),
  },
  {
    name: "2.3.0",
    notes: (
      <>
        <Separator>Nouveautés</Separator>
        <li>
          Il est désormais possible de trier les lettres vertes mises en avant
          en les glissant/déposant.
        </li>
        <li>
          Lorsque vous trouvez un anagramme, les lettres valides sont
          automatiquement mises en avant.
        </li>
        <li>
          Lorsque vous trouvez un <b>nouvel</b> anagramme, une lettre du
          mot-mystère est marquée en <b style={{ color: "#b5f6ff" }}>bleu</b>.
          <br />
          Une lettre bleue indique qu'<b>elle est correctement placée</b> dans
          le mot.
          <br />
          Les lettres bleues mises en avant ne bougent pas lorsqu'on effectue un
          mélange.
        </li>
        <li>
          <b>
            Proposer des mots qui n'existent pas ne compte désormais plus comme
            une tentative&nbsp;!
          </b>{" "}
          <i>(c'était quelque chose de très demandé&nbsp;!)</i>
        </li>
        <Separator>Corrections de bugs</Separator>
        <li>
          La liste des mots suggérés ne devrait plus s'afficher sur une seule
          colonne, même pour les parties à 6 ou 7 lettres.
        </li>
      </>
    ),
  },
  {
    name: "2.2.1",
    notes: (
      <>
        <Separator>Nouveautés</Separator>
        <li>
          Les options affichent toutes les couleurs de monstre à débloquer et
          indiquent le succès à réussir pour les obtenir.
        </li>
        <li>
          Les succès <i>"Il y a combien de lettres, déjà&nbsp;?"</i> et{" "}
          <i>"Je n'abandonnerai pas&nbsp;!"</i> sont plus simples à obtenir.{" "}
          <i>(allez-vous réussir à les débloquer&nbsp;?)</i>
        </li>
        <Separator>Corrections de bugs</Separator>
        <li>
          La sélection du mot du jour a été mise à jour pour éviter de favoriser
          les mêmes mots.
          <i>(on en a marre du mot DÔMES&nbsp;!)</i>
        </li>
      </>
    ),
  },
  {
    name: "2.2.0",
    notes: (
      <>
        <Separator>Nouveautés</Separator>
        <li>
          Quatre nouveaux succès sont à débloquer.{" "}
          <b>
            De plus, chaque succès obtenu vous permet d'appliquer une nouvelle
            couleur sur le petit monstre&nbsp;!
          </b>
        </li>
        <li>
          Les boutons du clavier pour valider un mot ou effacer une lettre sont
          d'une couleur différente.{" "}
          <i>(certains nouveaux joueurs ne les voyaient pas&nbsp;!)</i>
        </li>
        <li>
          Si cette nouvelle couleur ne vous plait pas, vous pouvez la modifier
          dans les options du jeu.{" "}
          <i>(vous pouvez aussi modifier la couleur des lettres&nbsp;!)</i>
        </li>
        <li>
          La disposition par défaut des boutons d'actions est désormais "Bouton
          d'effacement à gauche, bouton de validation à droite".{" "}
          <i>
            (vous pouvez changer cette disposition depuis les options du
            jeu&nbsp;!)
          </i>
        </li>
        <li>
          Vous pouvez désormais voir toutes vos tentatives depuis l'écran de fin
          d'une partie.
        </li>
        <li>
          Un compteur est affiché dans le champ de suggestion pour vous éviter
          de compter par vous-même le nombre de lettres que contient le mot que
          vous vous apprêtez à poposer.
        </li>
        <Separator>Corrections de bugs</Separator>
        <li>
          Le choix de la disposition des boutons de validation et d'effacement
          est correctement pris en compte lorsque les lettres vertes sont mises
          en avant.
        </li>
      </>
    ),
  },
  {
    name: "2.1.0",
    notes: ({ onRockClick, onRockExploded, isRockActive = false }) => (
      <>
        <Separator>Nouveautés</Separator>
        <li>
          Le dictionnaire du jeu a été retravaillé et les mots-mystères sont
          maitenant sélectionnés parmi les mots les plus communément utilisés
          dans les livres publiés entre 2000 et 2024. Le jeu devrait donc être
          globalement plus simple&nbsp;!
        </li>
        <li>
          Les options proposent désormais d'inverser les touches de validation
          et d'effacement du clavier.
        </li>
        <li>
          Un lien vers l'historique de toutes les notes de version a été ajouté
          dans l'écran des options.
        </li>
        {onRockClick && onRockExploded && (
          <>
            <Separator>Tiens, qu'est-ce que c'est que ça&nbsp;?</Separator>
            <NoStyleLi>
              <Box sx={{ textAlign: "center" }}>
                <MysteriousRock
                  onClick={isRockActive ? undefined : onRockClick}
                  className={isRockActive ? "is-active" : ""}
                  onAnimationEnd={onRockExploded}
                />
              </Box>
            </NoStyleLi>
          </>
        )}
        <Separator>Corrections de bugs</Separator>
        <li>
          Le bouton pour partager son résultat du "mot du jour" est de retour.
        </li>
        <li>
          L'écran défile correctement vers le haut lors d'un changement de page.
        </li>
      </>
    ),
  },
  {
    name: "2.0.0",
    notes: (
      <>
        <li>
          Le clavier du jeu peut maintenant être utilisé pour suggérer des mots.
          Il n'est plus nécessaire d'appuyer sur la zone de saisie afin d'ouvrir
          le clavier de votre appareil.{" "}
          <i>(mais cela est toujours possible si vous le souhaitez&nbsp;!)</i>
        </li>
        <li>
          <b>
            Marquer les lettres en rouge ou vert se fait désormais via un appuis
            long sur la touche du clavier, suivi d'un déplacement vers la gauche
            ou la droite de l'écran.
          </b>{" "}
          <i>(comme dans Tinder&nbsp;!)</i>
        </li>
        <li>
          Les options permettent désormais de choisir la disposition des touches
          du clavier du jeu.
        </li>
        <li>
          L'écran des options affiche désormais le numéro de version du jeu.
        </li>
        <li>
          Les parties en mode facile, normal ou difficile sont désormais
          sauvegardées. Vous pouvez ainsi quitter une partie en cours et la
          reprendre plus tard&nbsp;!{" "}
          <i>
            (auparavant, seul le mode "mot du jour" bénéficiait de cette
            fonctionnalité)
          </i>
        </li>
        <li>
          Le jeu tourne maintenant sur un tout nouveau moteur&nbsp;! Les
          performances et les animations devraient être améliorées.
        </li>
      </>
    ),
  },
];
